<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/youtube')" class="current">유튜브</li>
			<li @click="goto('/admin/column')">칼럼</li>
			<li @click="goto('/admin/writer')">칼럼 작성자</li>
		</ul>

		<div class="contents-container">
			<p class="contents-title underline marB-20 body3">유튜브 콘텐츠 등록</p>
			<div class="input-container marB-40">
				<section class="marB-40">
					<div class="input-item align-top">
						<AddImageSingle :refName="'mainImage'" @mainImage="setMainImage" />
					</div>
				</section>

				<section class="basic-info">
					<div class="input-item">
						<p class="small-title">유튜브 영상 업로드 일자</p>
						<input
							type="text"
							class="admin-input small"
							name="contents"
							placeholder="YYYY-MM-DD"
							v-model="uploadDate"
						/>
					</div>

					<div class="input-item">
						<p class="small-title">제목</p>
						<input
							type="text"
							class="admin-input medium"
							name="title"
							v-model="title"
						/>
					</div>
					<div class="input-item">
						<p class="small-title">설명 문구</p>
						<input
							type="text"
							class="admin-input medium"
							name="contents"
							v-model="contents"
						/>
					</div>
					<div class="input-item">
						<p class="small-title">영상 링크</p>
						<input
							type="text"
							class="admin-input medium"
							name="link"
							v-model="link"
						/>
					</div>
				</section>
				<LoadingSpinner :loading="isLoading" />
				<button class="outlined-btn body3" @click="addYoutube">등록</button>
			</div>
		</div>
	</div>
</template>

<script>
import AddImageSingle from '@/components/common/AddImageSingle';
import { mapGetters } from 'vuex';
import Store from '@/store';
import { addYoutube } from '@/api/youtube';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	components: { LoadingSpinner, AddImageSingle },
	computed: {
		...mapGetters(['main']),
	},
	created() {
		Store.commit('SET_MAIN', null);
	},
	data: () => ({
		title: '',
		link: '',
		uploadDate: '',
		contents: '',
		errMsg: '',
		isLoading: false,
	}),
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async addYoutube() {
			if (!this.main) {
				this.errMsg = '이미지를 등록해주세요';
				alert(this.errMsg);
				return false;
			}

			if (!this.title) {
				this.errMsg = '제목을 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.link) {
				this.errMsg = '링크를 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.uploadDate) {
				this.errMsg = '업로드한 날짜를 입력해주세요';
				alert(this.errMsg);
				return false;
			}
			if (!this.contents) {
				this.errMsg = '설명 문구를 입력해주세요';
				alert(this.errMsg);
				return false;
			}

			let data = new FormData();
			data.append('file', this.main);
			data.append('title', this.title);
			data.append('contents', this.contents);
			data.append('link', this.link);
			data.append('uploadDate', this.uploadDate);
			let result;
			this.isLoading = true;
			try {
				result = await addYoutube(data);
				if (result.data.successYn == 'Y') {
					alert('유튜브가 등록되었습니다');
					await this.initForm();
				} else {
					alert(result.data.msg);
				}
			} catch (e) {
				alert('에러가 발생했습니다');
			} finally {
				this.isLoading = false;
			}
		},
		initForm() {
			this.title = '';
			this.link = '';
			this.contents = '';
			this.uploadDate = '';
			Store.commit('SET_MAIN', '');
		},
		setMainImage(data) {
			Store.commit('SET_MAIN', data);
			console.log(this.main);
		},
	},
};
</script>

<style scoped>
.basic-info {
	margin-bottom: 40px;
}

.options {
	margin-bottom: 40px;
}
</style>
