// 메인화면

import { instance, instanceWithAuth } from './index';

function fetchYoutubeTotalCount() {
	return instance.get(`api/youtube/totalCount`);
}

function fetchYoutube(page) {
	return instance.get(`api/youtube/all/${page}`);
}

function fetchYoutubeDetail(id) {
	return instance.get(`api/youtube/detail/${id}`);
}

function addYoutube(data) {
	return instanceWithAuth.post('api/admin/youtube/new', data);
}

function updateYoutube(id, data) {
	return instanceWithAuth.put(`api/admin/youtube/update/${id}`, data);
}

function deleteYoutube(data) {
	return instanceWithAuth.delete('api/admin/youtube/delete', { data });
}

export {
	fetchYoutubeTotalCount,
	fetchYoutube,
	fetchYoutubeDetail,
	addYoutube,
	updateYoutube,
	deleteYoutube,
};
